.profile {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
    text-align: center;
  }
  
  .home-name {
    font-size: 45px;
    font-weight: bold;
    color: white;
  }

  .home-funcion{
    font-size: 75px;
    font-weight: bold;
    color: yellow;
  }
  
  .profile-text span {
    color: #ff6b6b;
  }
  
  .profile-image {
    width: 300px;
    height: auto;
    border-radius: 50%;
    margin-top: 20px;
  }
  
  @media (max-width: 768px) {
    .home-funcion {
      font-size: 30px;
    }
    .home-name{
      font-size: 24px;

    }
    .profile-image {
      width: 200px;
    }
  }
  
  .home-container {
    margin: 20px;
  }
  
  